body {
  text-align: center;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: rgb(205,133,63); 
}

@media screen and (min-width: 320px) and (max-width: 1024px) and (orientation: portrait) {
  html {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    -webkit-transform-origin: left top;
            transform-origin: left top;
    width: 100vh;
    overflow-x: hidden;
    position: absolute;
    top: 100%;
    left: 0;
  }
}
body {
  text-align: center;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: "Comic Sans MS", cursive, sans-serif;
  overflow: hidden;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

h1 {
	color: green;
	font-size: 2.6em;
	padding-top: 5%;
}

.timesup {
	padding-top: -5%;
}

.firstOne, .secondOne {
	font-size: 1.1em;
	padding-left: 30px;
}

.stat {
	position: absolute;
	font-size: 1.4em;
	color: grey;
	top: 0;
}

.stat:active {
	text-shadow: 0px 0px 10px blue; 
}

.sound {
	top: 0;
	right: 25px;
}

.score {
	left: 25px;
}

.play {
	color: purple;
	font-size: 3.2em;
	margin: 0 auto;
	width: 100px;	
	margin-top: 10%;
}

.replay {
	margin-top: 0%;
	width: 130px;
}

.play:hover {
	font-size: 3.4em;
	text-shadow: 0px 0px 20px red;
}

@media screen and (max-device-width: 600px) {
	h1 {
		margin-top: 1%;
	}
}

@media screen and (min-device-width: 900px) {
	h1 {
		font-size: 4em;
		padding-top: 8%;
	}

	.stat {
		position: absolute;
		font-size: 1.4em;
		color: grey;
		top: 0;
	}

	.play {
		font-size: 4em;
		margin-top: 10%;
	}

	.sound {
		top: 5px;
		right: 50px;
		font-size: 2.3em;
	}

	.score {
		left: 30px;
		font-size: 2.3em;
	}
}
.btn {
	background-color: darkgrey;
	border-radius: 3%;
	border: none;
	padding: 10px 20px 10px 20px;
	margin-left: 1px;
	margin-right: 1px;
	outline: none;
	font-size: 1.1em;
	font-family: "Comic Sans MS", cursive, sans-serif;
}

.btn:active {
	outline: none;
	background-color: grey;
	border: none;
}

.showed {
	display: block;
}

.hidden {
	display: none;
}

.container {
	width: 450px;
	margin: auto;
	margin-top: 50px;
	font-size: 3em;
	display: grid;
	justify-self: center; 
	justify-items: center;
  grid-template-columns: 155px 155px 150px;
  grid-template-rows: 140px 140px 140px;
}

.hole {
	background-color: black;
	width: 90px;
	height: 90px;
	border-radius: 50%;
	z-index: 3;
}

.eboss {
	height: 68px;
	margin-top: 14px;
	width: 68px;
	border-radius: 50%;
}

.first {
	margin-top: -8px;
	margin-right: 20px; 	
}

.second {
	margin-top: 5px;
	margin-left: 3px; 	
}

.third {
	margin-top: -15px;
	margin-right: -5px; 	
}

.fourth {
	margin-top: 5px;
	margin-right: 15px; 	
}

@media screen and (max-device-width: 570px) {
	.hole {
		width: 80px;
		height: 80px;
	}

	.eboss {
		height: 60px;
		width: 60px;
		margin-top: 14px;
	}

	.container {
	  grid-template-columns: 150px 140px 155px;
	  grid-template-rows: 100px 100px 105px;
	}
}

@media screen and (min-device-width: 701px) {
	.hole {
		width: 100px;
		height: 100px;
	}

	.eboss {
		height: 70px;
		width: 70px;
		margin-top: 14px;
	}

	.container {
	  grid-template-columns: 145px 190px 190px;
	  grid-template-rows: 150px 150px 150px;
	}
}

@media screen and (max-device-height: 380px) and (min-device-width: 620px) {
	.container {
		margin-bottom: 10px;
	  grid-template-rows: 130px 130px 130px;
	}
}

@media screen and (min-device-width: 900px) {
	.hole {
		width: 140px;
		height: 140px;
	}

	.eboss {
		height: 100px;
		width: 100px;
		margin-top: 14px;
	}

	.container {
		margin-left: 200px;
		margin-top: 100px;
		justify-items: end;
	  grid-template-columns: 150px 300px 300px;
	  grid-template-rows: 300px 300px 300px;
	}
}

@media screen and (min-device-width: 1200px) {
	.hole {
		width: 180px;
		height: 180px;
	}

	.eboss {
		height: 130px;
		width: 130px;
		margin-top: 20px;
	}

	.container {
		margin-left: 200px;
		margin-top: 100px;
		justify-items: end;
	  grid-template-columns: 250px 400px 400px;
	  grid-template-rows: 300px 300px 300px;
	}
}

